import React from "react";
import Box from "@material-ui/core/Box";
import { Container, TextField, FormGroup, Card, Grid, Button, withStyles, IconButton, Typography } from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

const formInputCommonStyle = {
    "& label.Mui-focused": {
        color: "##1E90FF",
    },
    "& .MuiInput-underline:after": {
        borderBottomColor: "##1E90FF",
    },
    "& .MuiOutlinedInput-root": {
        "& fieldset": {
            borderColor: "#1E90FF",
        },
        "&:hover fieldset": {
            borderColor: "#1E90FF",
        },
        "&.Mui-focused fieldset": {
            borderColor: "##1E90FF",
        },
    },
};

const useStyles = (theme) => ({
    root: {
        "& .MuiTextField-root": {
            margin: theme.spacing(1),
        },
    },
    demoWrapText: {
        backgroundColor: "#1E90FF",
        color: "#ffffff",
        textTransform: "none",
        fontSize: "23px",
        top: "0",
        left: "0",
        width: "100%",
        height: "300px",
        opicity: 1,
        position: "absolute",
    },
    demoWrapImg: {
        width: "100%",
        margin: "280px auto",
        padding: "10px",
        zIndex: 2,
        position: "relative",
        textAlign: "center",
        marginBottom: "0",
        background: "#ffffff",
        borderRadius: "5%",
        maxWidth: "100%"
    },
    buttonProgress: {
        color: " green[500]",
        position: "absolute",
        top: "50px",
        left: "50%",
        marginLeft: -16,
    }
});

const CssTextField = withStyles({
    root: formInputCommonStyle,
})(TextField);

class Login extends React.Component {

    constructor(props) {
        super(props);
        this.inputStyles = { marginBottom: "20px" };
        this.state = {
            userName: "",
            open: false,
            errorMessage: null,
            loading: false
        };
    }

    handleClose(_event, reason) {
        if (reason === "clickaway") {
            return;
        }
        this.setState({ open: false });
    }

    handleChangeFormData(e) {
        const inputName = e.target.name;
        this.setState({ [inputName]: e.target.value.trim() });
    }
    performSearch(e) {
        e.preventDefault();
        const self = this;
        const memberUrl = `https://tarabartest.firebaseio.com/Members/${this.state.userName}.json`;
        fetch(memberUrl)
            .then((response) => response.json())
            .then((result) => {
                console.log("performSearch() value=", result);
                if (result && result !== "null" && result !== "") {
                    this.props.history.push(`/recoveryAccount/${this.state.userName}`);
                } else {
                    console.log("Member is not found!");
                    self.setState({ open: true, errorMessage: "Member is not found!", loading: false });
                }
            }, (error) => {
                console.error("performLogin()", error);
                self.setState({ open: true, errorMessage: "Sorry, server cannot be communicated.", loading: false });
            });
    }

    render() {
        const { classes } = this.props;
        return <React.Fragment>
            <Card style={{ background: "white" }}>
                <Box>
                    <Container className={classes.demoWrapImg}>
                        <Grid container>
                            <Grid item xs={1} md={4} lg={4} >
                                <Box style={{ width: "100%" }}></Box>
                            </Grid>
                            <Grid item xs={10} md={4} lg={4} >
                                {/* <form autoComplete="off" onSubmit={(e) => this.performSearch(e)}> */}
                                <Box m={3}></Box>
                                <FormGroup style={this.inputStyles}>
                                    <CssTextField
                                        className={classes.margin}
                                        label="User Name"
                                        variant="outlined"
                                        autoFocus={true}
                                        name="userName"
                                        id="user-name-outlined-input"
                                        required
                                        onChange={(event) => this.handleChangeFormData(event)}
                                    />
                                </FormGroup>
                                <div style={{ position: "relative" }}>
                                    <Button type="submit" variant="text" style={{ backgroundColor: "#1E90FF", color: "#fff", textTransform: "none", width: "100%", fontSize: "23px", margin: "10mm 0 5mm 0", opacity: (this.state.loading ? "0.5" : "1") }} onClick={(e) => this.performSearch(e)}>
                                        Search
                                    </Button>
                                    {this.state.loading && <CircularProgress size={30} className={classes.buttonProgress} />}
                                </div>
                                {/* </form> */}
                            </Grid>
                        </Grid>
                    </Container>
                    <Box className={classes.demoWrapText} >
                        <IconButton onClick={() => { this.props.history.goBack(); }}>
                            <ArrowBackIcon style={{ color: "#ffffff" }} />
                        </IconButton>
                        <Box m={2}>
                            <Typography variant="h4" align="center" className={classes.title}>
                                Password Recovery
                            </Typography>
                        </Box>
                    </Box>
                </Box >

            </Card>
            <Snackbar open={this.state.open} autoHideDuration={6000} onClose={() => this.handleClose()}>
                <MuiAlert elevation={6} variant="filled" onClose={() => this.handleClose()} severity="error">
                    {this.state.errorMessage || "Sorry, something went wrong."}
                </MuiAlert>
            </Snackbar>
        </React.Fragment>;
    }
}

export default withStyles(useStyles, { withTheme: true })(Login);
