import React, { Component } from "react";
import {
    BrowserRouter,
    Switch,
    Route,
} from "react-router-dom";
import RequestUserName from "./pages/RequestUserName";

import RecoveryAccountWithPhoneNo from "./pages/RecoveryAccountWithPhoneNo";

class App extends Component {
    constructor(props) {
        super(props);
        window.ref = document.referrer.split("/tarabar")[0];
    }

    render() {
        return <BrowserRouter basename="/web-phone-verify">
            <Switch>
                <Route path="/recoveryAccount/:userName" component={RecoveryAccountWithPhoneNo} />
                <Route component={RequestUserName} />
            </Switch>
        </BrowserRouter>;
    }
}

export default App;
